import React, {useEffect } from 'react';
import M from 'materialize-css'
import { Link } from 'react-router-dom';


function Navigate(){
    let instances = null;
    useEffect(() => {
        
        let options = {};
       
            var elems = document.querySelector('.sidenav');
            instances = M.Sidenav.init(elems);     
      }, []);
      let closeNav= ()=>{
        console.log('hey');
        if(instances)
        instances.close();
    };
    let openNav= ()=>{
       console.log('test');
        if(instances)
        instances.open();
        else{
          var elems = document.querySelector('.sidenav');
          instances = M.Sidenav.init(elems); 
          instances.open();    
        }
    };
     
     

    return(
        <div>
      <ul id="slide-out" className="sidenav">
  <li><div className="user-view">
    <div className="background blue">
      {/* backgroun */}
      <a><i  style={{float:"right", padding:"15px"}} class="material-icons white">close</i></a>
    </div>
    
    <a  ><img className="circle" src="https://lh3.googleusercontent.com/proxy/T6p-jcq2zW5iDi7NYlNwgiuPeR0l82nH-xlhcNkIYdzI9lP2yrFmdnx5UsKQwNHhW59RYhxM6xuwY-KEhwrMPeImCBrEK8rkMvtBBZHJfEYkwUM3YvCC6qsjfn3uTFJm_Uep8VrsN4SiqUdg3DID7TUJ" /></a>
    <a href="#name"><span className="black-text name">Marees</span></a>
    <a href="#email"><span className="black-text email">adasj@ahd.com</span></a>
  </div></li>
  <Link onClick={()=>closeNav()} to = "/nav">
  <li><a href="#"><i className="material-icons">cloud</i>Home</a></li>
  </Link>
  <Link onClick={()=>closeNav()} to = "/order">
  <li><a href="#"><i className="material-icons">cloud</i>My Orders</a></li>
  </Link>

  <Link onClick={()=>closeNav()} to = "/cart">
  <li>
  <a><i className="material-icons">cloud</i>Cart</a>
  </li>
  </Link>
 

  <li><div class="divider"></div></li>
  <li><a class="subheader">More</a></li>
  <li><a class="waves-effect" href="#!">Contact</a></li>
  <li><a class="waves-effect" href="#!">Help</a></li>
  <li><a class="waves-effect" href="#!">Logout</a></li>

</ul>
<nav class="blue darken-5">
<div class="nav-wrapper">

      <a onClick={()=>openNav()} style={{padding:"0 35px", float:"left"} } class="brand-logo left"><i class="material-icons ">menu</i></a>
    </div>
</nav>

           </div>
    );
}

export default Navigate;