import React, { useState, useEffect } from 'react';
import Navigate from './Navigate';
import Product from './Product';


function Dashboard() {

    

    const [mode, setMode] = useState('all');

    let generateClass = (name)=>{
        if(mode === name)
            return 'blue text-white';
        else    
            return 'grey'
       
    }

    return (
        
        <div class="home" style={{ backgroundColor: "#e1f5fe" }}>
          <Navigate />
            <div style={{ backgroundColor: "#fff" }} class="container mt-5 mb-5">

                <div class="row">
                    
                    <div onClick={()=>setMode('all')} class={`${generateClass('all')} chip chipWs`}>
                        <img class="circle" src="https://cdn3.vectorstock.com/i/1000x1000/07/67/small-water-bottle-vector-3300767.jpg" alt="Contact Person" />
    All
  </div>
                    <div onClick={()=>setMode('10')} class={`${generateClass('10')} chip chipWs`}>
                        <img src="https://cdn3.vectorstock.com/i/1000x1000/07/67/small-water-bottle-vector-3300767.jpg" alt="Contact Person" />
    10Ltrs
  </div>
                    <div onClick={()=>setMode('5')} class={`${generateClass('5')} chip chipWs`}>
                        <img src="https://cdn3.vectorstock.com/i/1000x1000/07/67/small-water-bottle-vector-3300767.jpg" alt="Contact Person" />
    5Ltrs
  </div>
                    <div onClick={()=>setMode('1')} class={`${generateClass('1')} chip chipWs`}>
                        <img src="https://cdn3.vectorstock.com/i/1000x1000/07/67/small-water-bottle-vector-3300767.jpg" alt="Contact Person" />
    1Ltr
  </div>
                </div>


<Product />
            </div>



        </div>
    );
}


export default Dashboard;