import { FETCH_PRODUCTS, UPDATE_PRODUCTS, GET_CART } from '../actions/types'

const authReducer = (state={
    products: []
}, action) => {
   
    switch(action.type){
        case FETCH_PRODUCTS:
            let proData = [];
            console.log(action.payload);
            if(action.payload){
                action.payload.map((data)=>{
                    if(!data.quantity){
                        data.quantity = 0;
                        data.totalPrice = 0;
                    }
                    proData.push(data);
                })
            }
            /// modify quantity here
            state = {
                ...state,  
                products: proData || false
            }
           
            // and return it
            return state;


        case UPDATE_PRODUCTS:
            console.log(state);
            return state;

        case GET_CART:
            console.log(state)
            return state;
        default:
            console.log('-->>> STEP 1.1: @REDUCER Initial State is assigned to store our state is :');
            console.log('-->>> STEP 1.2: @REDUCER State is sent to store');
            return state;
    }
}

export default authReducer;