import React, { useState, useEffect, useRef } from "react";
import Navigate from "./Navigate";
import * as actions from '../../actions';
import { connect } from 'react-redux';
function Cart(props){
    const [quant, setQuant] = useState(false);


       
//     //update
//     const mounted = useRef();
// useEffect(() => {
//   if (!mounted.current) {
//     mounted.current = true;
//   } else {

//     console.log('update');
//     console.log(props.products);
//    // setHtml(generateProducts());
//   }
// });

let cartItems = [];

    
   

       let cartChange = (mode, id)=>{
        console.log(id);
     
        
       
        cartItems.map((cart) => {
            
            if(cart._id === id){
                console.log(cart)
                if(mode === 'add')
                    cart.quantity++;
                else 
                    if(cart.quantity>0){
                        cart.quantity--;
                    } else{
                        cart.totalPrice = 0;
                    }

            cart.totalPrice = cart.quantity * cart.price;

            }
           // let position;
            //setPos(position);
            setQuant(!quant);
            console.log(cartItems);
           props.updateProducts(cartItems);
        });
  
   }

    let generateCartItems= ()=>{
        cartItems = props.products;
        return cartItems.map((data,position)=>{
            return(
                <div>
                <div class="p-1 col m-0 col-sm-12 col-xl-12 col-lg-12 col-md-12 cart-row">
                        
                <div class="col col-sm-6 col-lg-3 col-md-3">
                    <div class="center text-white bg-dark cart-product-container">
            <h6>{data.name}</h6>
            <h7>{data.type}</h7>
                      <img class="product-img" src={data.img}/>
                      </div>
                </div>
                <div class="my-holder">
                <div className="add-menu">
                <div class="btn-group" role="group" aria-label="...">
                <button onClick={()=>cartChange('remove', data._id)} type="button" class="btn btn-lg btn-secondary">-</button>
            <button type="button" class="btn text-dark btn-lg bg-white btn-secondary">{data.quantity}</button>
                <button onClick={()=>cartChange('add', data._id)} type="button" class="btn btn-lg btn-secondary">+</button>

                </div>
</div>
            <h4 class="text-center">{data.totalPrice}</h4> 
                </div>
               

 
                </div>
                  
                 <div class="blue center mt-3 mr-5 ml-5" style={{height: '2px'}}></div>
                 </div>
            );
        });
    }
   return(
       <div>
           <Navigate />
           <div class="my-container container mt-5 mb-5">
                         {generateCartItems()}

                         <div class="mt-5  btn d-block">Checkout ₹400</div>
             

                    
      </div>
       </div>
   )
}
function mapStateToProps(myState){
    console.log(myState.authReducer.products);
    return { products: myState.authReducer.products};       //// see consolelog and fincd the crct obj
  }
  
  export default connect(mapStateToProps, actions)(Cart);
