import React, { useState, useRef, useEffect } from "react";
import { connect } from 'react-redux';
import * as actions from '../../actions';

function Product(props){
    const [isTrigger, setTrigger] = useState(false);
    const [quant, setQuant] = useState(false);
    const [getPos, setPos] = useState();
    
    const myCartQuant = useRef(null);
    let initialQuantz = [];


    let products = [];

  
//     //update
//     const mounted = useRef();
// useEffect(() => {
//   if (!mounted.current) {
//     mounted.current = true;
//   } else {
//     console.log('update');
//     products = props.products;
//    // setHtml(generateProducts());
//   }
// });


useEffect(() => {
    props.fetchProducts();
    console.log(props); 
   
   
  }, []);

    let cartChange = (mode, id)=>{
        console.log(id);
     
        
       
        products.map((cart) => {
            
            if(cart._id === id){
                console.log(cart)
                if(mode === 'add')
                    cart.quantity++;
                else 
                    if(cart.quantity>0){
                        cart.quantity--;
                    } else{
                        cart.totalPrice = 0;
                    }
                      

                cart.totalPrice = cart.quantity * cart.price;

            }
           // let position;
            //setPos(position);
            setQuant(!quant);
            console.log(products);
           props.updateProducts(products);
        });
  
   }

    let triggerCart = (position)=>{
       setTrigger(!isTrigger);
      setPos(position);
      
    }
    let triggerQuant = (position)=>{
       
        setPos(position);
        setQuant(!quant);

    }

  
    
    let generateClass = (position, mode)=>{
        if(mode === ''){
            if(quant)
                return "hide"
            
        }
        if((position === getPos)){
            if(mode === 'cart'){
                return 'hide';
            } else if(mode === ''){
                return 'hide';
            }
               return "show";
        }
        else{
            if(mode === 'cart'){
                return 'show';
            } 
               return "hide"
        }
    }
   
  
    
    let generateProducts = ()=>{
        products = props.products;
        console.log('test');
        return products.map((pro, position)=>{
            return(
                <div class="card col-lg-4 col-md-4 col-sm-12">
      
                <div class="card-body">
                    <div class="justify-content-center d-flex">
                <img style={{height:"150px", width:'auto'}} src= {pro.img} class="card-img-top" alt="..." />
                </div>
                  <h5 class="card-title">{pro.name}</h5>
                  <p class="card-text">{pro.type}</p>
                  <h4 class="card-title">{pro.price}</h4>
                  
                  <a onClick={() => (triggerCart(position))} class={`${generateClass(position, 'cart')} d-block btn btn-primary`}>Add To Cart</a>
                  
                 <div className={`${generateClass(position, 'qnt')} add-menu-${position}`}>
                  <div class="justify-content-center d-flex p-3">
                      
                    <a onClick={()=>cartChange('remove', pro._id)}  class="m-1 valign-wrapper col col-lg-3 col-md-3 col-sm-4 waves-effect waves-light btn" >
                    <i class="material-icons tiny">remove</i>
                    </a>
                      <p class={`m-1 btn white black-text text-center center col col-lg-3 col-md-3 col-sm-4 quant-${position}`}>{pro.quantity}</p>
                    <a onClick={()=>cartChange('add', pro._id)} class="m-1 valign-wrapper col col-lg-3 col-md-3 col-sm-4 waves-effect waves-light btn" >
                    <i class="material-icons tiny">add</i>
                    </a>
                
                  </div>
                  <div class="justify-content-center d-flex">
                  <a onClick={()=>{triggerQuant()}} class={`${generateClass(position, 'qnt')} m-2 btn bg-danger btn-primary`}>Cancel</a>
                  <a onClick={()=>{triggerQuant()}} class={`${generateClass(position, 'qnt')} m-2 btn bg-success btn-primary`}>Add</a>
                  </div>
                  </div>
                   </div>
                
              </div>
                              
            )
        })
      
    }

    return(
        
           <div class="row pl-5 pr-5"> 
          {
          generateProducts()
        }
      </div> 
           
    );
}

function mapStateToProps(myState){
    console.log(myState.authReducer.products);
    return { products: myState.authReducer.products};       //// see consolelog and fincd the crct obj
  }
  
  export default connect(mapStateToProps, actions)(Product);