import React from 'react';
import { useHistory } from 'react-router-dom';

import Button from 'react-bootstrap/Button';
function Water(){
    let history = useHistory();

    let routeChange = (event)=>{
            let path = `nav`;
            history.push(path);
    };

    return(
        <div style={{
            backgroundColor: "#405C69"
          }} class="full-width col col-lg-6 col-md-6">
           <div class="container">
               <h1 class="white-text">Login</h1>
    <form style={{padding:"5px 25px", backgroundColor:"#EAEFF9"}} >
     
    <div class="row">

    <div class="input-field">
          <i class="material-icons prefix">account_circle</i>
          <input id="icon_prefix" type="text" class="validate" />
          <label for="icon_prefix">Username</label>
        </div>
    </div>
        <div class="row">
        <div class="input-field">
          <i class="material-icons prefix">account_circle</i>
          <input id="icon_prefix" type="password" class="validate" />
          <label for="icon_prefix">Password</label>
        </div>
        </div>
        
     <div class="row">
         <div class="col s12 m12 l12 xl12">
         <a onClick={()=>routeChange()} style={{backgroundColor:"#405C69"}} class="col s4 m4 l4 xl4 waves-effect waves-light white-text btn">Login</a>
         <p>Or Login using</p>
         <div class="row col offset-s5 offset-m5 offset-l5 offset-xl5">
         <i style={{padding:"0 5px"}} class="material-icons">add</i>
         <i style={{padding:"0 5px"}} class="material-icons">add_a_photo</i>
         <i style={{padding:"0 5px"}} class="material-icons">add_alarm</i>
         </div>
         </div>
     
     </div>
    </form>
    </div>
           </div>
    );
}

export default Water;
